import {Component} from '@angular/core';
import {NgUploaderOptions} from 'ngx-uploader';
import {PostalService} from "../../_services/postal.service";
import {PublishTarget} from "../../../globals";

@Component({
  selector: 'plugin-uploader',
  styleUrls: ['./plugin-uploader.scss'],
  templateUrl: './plugin-uploader.html'
})
export class PluginUploader {
  public fileUploaderOptions: NgUploaderOptions = {
    url: '/upload',
  };

  constructor(private postalService: PostalService) {
  }

  rebuildClient() {
    this.postalService.publish('System', 'RebuildClient', {}, PublishTarget.server);
  }
}



