import {AfterViewInit, Component} from '@angular/core';
import {PostalService} from "../../../../_services/postal.service";
import {PublishTarget} from "../../../../../globals";

@Component({
  selector: 'plugin-table',
  templateUrl: './pluginTable.html'
})
export class PluginTable implements AfterViewInit {

  pluginTableData = [];

  constructor(private postalService: PostalService) {
    const me = this;
    me.postalService.subscribe('PluginTable', 'PluginList', (pluginTableData) => {
      me.pluginTableData = pluginTableData;
    });
  }

  ngAfterViewInit(): void {
    this.postalService.publish('PluginManager', 'GetPluginList', {}, PublishTarget.server);
  }
}



